.optionalExtrasGrid{
    display: grid;
    margin-top: 2rem;
    grid-template-columns: repeat(4, 8rem);
    grid-template-rows: 2rem repeat(3, 6rem);
    justify-content: left;
    padding-left: 10px;
}
/* .optionalExtrasGrid > button {
    background-color: rgba(255, 255, 255, .75);
    border: 1px solid white;
    cursor: pointer;
    outline: none;
} */
.optExtraBtn{
    background-color: rgba(255, 255, 255, .75);
    border: 1px solid white;
    cursor: pointer;
    outline: none;
}
.optExtraBtnSelected{
    background-color: white;
}
.optionalExtrasGrid > button:hover,
.optionalExtrasGrid > button:hover{
    background-color: rgba(255, 255, 255, .9);
}
.spanFour{
    grid-column: span 4;
}
.paint{
    display: grid;
    margin-top: 2rem;
    grid-template-columns: repeat(4, 8rem);
    grid-template-rows: 2rem repeat(3, 6rem);
    justify-content: left;
    padding-left: 10px;
}
.paint > button {
    background-color: rgba(255, 255, 255, .75);
    border: 1px solid white;
    cursor: pointer;
    outline: none;
}
input{
    font-size: 30px;
    text-align: center;
}